'use strict';

define(['text!./view.html', 'knockout', 'app/config', 'app/data-context-labels'], function (template, ko, config, ctx) {

    function ViewModel() {

        var self = this;

        this.email = config.contact;
        this.emailLink = 'mailto:' + this.email;
        this.labels = ko.observableArray([]);

        ctx.labels.getSummaries().then(function (data) {

            self.labels(data);
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});